.skill-card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 5%;
  justify-content: center;
  margin: 80px 20%;
}

.skill {
  display: flex;
  flex-direction: column;
  width: 150px;
  align-items: center;
  margin-bottom: 20px;
}

.skill > img {
  object-fit: contain;
  width: 108px;
  height: 107px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  margin-bottom: 15px;
}

.skill > p {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  text-align: center;
  line-height: 36px;
  color: #000;
  letter-spacing: -0.015rem;
}

.skills-vector-frame {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  right: 0;
  bottom: 0;
  width: 80%;
  z-index: -10;
}

.skills-vector {
  max-height: 100%;
  width: 100%;
}

@media only screen and (max-width: 640px) {
  .skills-vector-frame {
    width: 90%;
  }

  .skill-card-container {
    margin: 80px 10%;
  }
}
