.section-container {
  display: flex;
  flex-direction: column;
  color: #060b26;
  font-weight: 700;
}

.text-center {
  display: flex;
  justify-content: center;
}

.contact-form-container {
  width: 50%;
  margin: 70px 0;
  align-self: center;
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input-box {
  background: #fff;
  width: 100%;
  border: 1px solid #e8e8e8;
  box-sizing: border-box;
  box-shadow: 0 2px 2px rgb(0 0 0 / 25%);
  border-radius: 40px;
  padding: 19px 35px;
  margin: 10px 0;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 16px;
}

.body-input {
  height: 250px;
  padding: 25px 35px;
}

.contact-btn {
  margin: 10px 0;
  border: none;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  width: 100%;
  text-align: center;
  padding: 17px 35px;
  border-radius: 35px;
  background: linear-gradient(39.97deg, #f0f8ff 0.79%, #72a0c1 79.29%);
}

.social-icons-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.social-icon {
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  margin: 21px;
}

.social-icon:hover,
.contact-btn:hover {
  filter: drop-shadow(0 8px 8px rgba(0, 0, 0, 0.32));
  cursor: pointer;
  transition: all 0.3s;
}

@media only screen and (max-width: 640px) {
  .social-icon {
    margin: 8px 21px;
  }

  .contact-form-container {
    width: 80%;
  }
}

.alert {
  text-align: center;
  padding: 10px;
  font-size: 0.875rem;
  background-color: hsl(250deg 69% 61%);
  color: #fff;
  border-radius: 1.1rem;
}
